module.exports = {
    data(){
        return {
            API_URL : process.env.API_URL || 'https://dev-situm-api.herokuapp.com',
            productImageBaseUrl: '/api/productimages/'
        }
    },
    methods: {  
        createImageURL(product){
            if(product.ProductImages < 1)
              return "../assets/images/recorte.png";
            var image = product.ProductImages[0].Image;
            return this.API_URL + this.productImageBaseUrl +  'getImage/' + image;
      
          }          
    }
}
