<template>
    <div>
        <nav class="navbar navbar-light bg-light">
      <a class="navbar-brand" href="#">
        <p class="m-0 head-title">USUARIO: <span>{{user.First_Name}} {{user.Last_Name}}</span></p>
      </a>
      <div class="my-2 my-lg-0">
        <p class="m-0 head-title">SALDO: <span>${{currentUserBalance}}</span></p>
      </div>
    </nav>
    <!-- End -->
    <div class="container-fluid">
      <div class="row align-items-top mt-2">
        <div class="col-8 text-white">
          <a class="text-white font-22 anchor-text" href="#">{{product.Name}}</a>
          <div class="product-info mt-5">
            <p>{{product.Description}}</p>
            <p class="mt-5">¡ESPERO DISFRUTES TU CERVEZA!</p>
          </div>
        </div>
        <div class="col-4 text-center">
          <a class="text-white font-22" href="#">DISPONIBLE: <span>{{total}} ml</span></a><br/>
          <div class="vertical-progress mt-3">
            <div class="progress-bar"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- Footer -->
    <footer class="footer">
      <div class="container">
        <div class="row">
          <div class="col-6 text-white pl-0">
            <img class="footer-logo" src="../assets/images/footer-logo.png" alt="Situm" >
          </div>
          <div class="col-6 text-white text-right pr-0">
            <span class="font-22" style="line-height: 64px;">AYUDA</span>
          </div>
        </div>
      </div>
    </footer>
    </div>
</template>
<script>
export default {
  name: "PouringScreen",
  props: ["currentUserBalance", "productDetails", "userDetails"],
  data: function () {
        return {
           product: this.productDetails.Product,
           tap: this.productDetails,
           total: 0,
           user: this.userDetails.CardCustomer[0].User
        }
    },
  mounted() {
      this.calculateTotalDisponsible();
      this.progressFill(0);
  },
  methods: {
      calculateTotalDisponsible(){
        var price = this.product.Price;
        var volume = this.tap.Volume * 1000;
        var balance = this.userDetails.CardCustomer[0].Amount;
        this.total = (balance / price) * 1000;
        var def = this.total - volume;
        if(def > 0){
          this.total = volume;
        }
      },
      progressFill(val){
           $(".progress-bar").css({'height': val+'%' });
      }
  },
  watch: {
        'currentUserBalance': function(val, oldVal){
           var t = this.userDetails.CardCustomer[0].Amount * 100;
           var def = (t) - val;
           var per = (def / t) * 100;
          this.progressFill(per);
      }
    }
};
</script>