<template>
  <div>
    <nav class="navbar navbar-light bg-light">
      <a class="navbar-brand" href="#">
        <p class="m-0 head-title">
          USUARIO:
          <span>{{user.First_Name}} {{user.Last_Name}}</span>
        </p>
      </a>
      <div class="my-2 my-lg-0">
        <p class="m-0 head-title">
          SALDO:
          <span>${{currentUserBalance}}</span>
        </p>
      </div>
    </nav>
    <!-- End -->
    <div class="container-fluid">
      <div class="row align-items-center">
        <div class="col-4">
          <img :src="createImageURL(product)" alt="Recorte">
        </div>
        <div class="col-8 text-white product-info">
          <h1>{{product.Name}}</h1>
          <p>{{product.Description}}</p>
          <p>{{product.Additional_Data}}</p>
          <p>PRECIO: ${{product.Price}} lt</p>
        </div>
      </div>
      <div class="row">
        <div class="col-6 pl-5">
          <a class="btn btn-warning text-white" @click="back()">VOLVER</a>
        </div>
        <div class="col-6 text-right pr-5">
          <a class="btn btn-warning text-white" @click="serve()">SERVIR</a>
        </div>
      </div>
    </div>
    <!-- Footer -->
    <footer class="footer">
      <div class="container">
        <div class="row">
          <div class="col-6 text-white pl-0">
            <img class="footer-logo" src="../assets/images/footer-logo.png" alt="Situm">
          </div>
          <div class="col-6 text-white text-right pr-0">
            <span class="font-22" style="line-height: 64px;">AYUDA</span>
          </div>
        </div>
      </div>
    </footer>
  </div>
</template>
<script>
import commonService from "../modules/common";
export default {
  name: "ProductDetailScreen",
  props: ["currentUserBalance", "productDetails", "userDetails"],
  data: function() {
    return {
      product: this.productDetails.Product,
      user: this.userDetails.CardCustomer[0].User
    };
  },
  mixins: [commonService],
  mounted() {},
  methods: {
    back() {
      this.$emit("goToBack");
    },
    serve() {
      this.$emit("serve");
    }
  }
};
</script>