import {HTTP} from "../../modules/api-service.js";
var baseUrl = '/api/customer/';

export default {
  state: {
       
  },
  getters: {
  },
  mutations: {
   
  },
  actions: {
    getModule({ commit }, deviceId) {       
        return new Promise((resolve, reject) => {
            HTTP.get(baseUrl + 'getModuleDetails/'+ deviceId).then(response => { 
                alert(JSON.stringify(response.data));
                resolve(response.data);               
            })
                .catch(e => {
                    alert(e);
                    reject(e);
                })
        })
    },
    getUserDetails({ commit }, cardRfId) {        
        return new Promise((resolve, reject) => {
            HTTP.get(baseUrl + 'getUserCardDetails/'+ cardRfId).then(response => {  
                alert(JSON.stringify(response.data));             
                resolve(response.data);
            })
                .catch(e => {
                    reject(e);
                })
        })
    },  
    saveConsumerHistory({ commit }, param) {
        return new Promise((resolve, reject) => {
            HTTP.post(baseUrl + 'add', param).then(response => {
                resolve(response.data);
            })
                .catch(e => {
                    reject(e);
                })
        })
    }    
  }
};