<template>
    <div>
         <nav class="navbar navbar-light bg-light">
      <a class="navbar-brand" href="#">
        <p class="m-0 head-title">USUARIO: <span>{{user.First_Name}} {{user.Last_Name}}</span></p>
      </a>
      <div class="my-2 my-lg-0">
        <p class="m-0 head-title">SALDO: <span>${{currentUserBalance}}</span></p>
      </div>
    </nav>
    <!-- End -->
    <div class="container-fluid">
      <div class="row">
        <div class="col-12 text-white">
          <h1 class="text-center mb-3 text-normal">ELIGA TU CERVEZA</h1>
        </div>
      </div>
      <div class="row">
        <div class="col-12 text-center">
          <div class="outer-block mb-5" @click="goToProductDetails(product1, 1)">
            <img :src="createImageURL(product1.Product)" alt="Recorte" >
            <span class="text-white">{{product1.Product.Name}}</span>
          </div>
          <div class="outer-block mb-5" @click="goToProductDetails(product2, 2)">
            <img :src="createImageURL(product2.Product)" alt="Recorte" >
            <span class="text-white">{{product2.Product.Name}}</span>
          </div>         
        </div>
      </div>
    </div>
    <!-- Footer -->
    <footer class="footer">
      <div class="container">
        <div class="row">
          <div class="col-6 text-white pl-0">
            <img class="footer-logo" src="../assets/images/footer-logo.png" alt="Situm" >
          </div>
          <div class="col-6 text-white text-right pr-0">
            <span class="font-22" style="line-height: 64px;">AYUDA</span>
          </div>
        </div>
      </div>
    </footer>
    </div>
</template>
<script>
import commonService from '../modules/common';

export default {
  name: "ProductListScreen",
  props: ["taps","currentUserBalance", "userDetails"],
  mixins: [commonService],
  data: function () {
      return {
          product1: this.taps[0],
          product2: this.taps[1],
          user: this.userDetails.CardCustomer[0].User
      }
  },
  mounted() {
      
  },
  methods:{
    goToProductDetails(product, tapNumber){
       this.$emit("goToProductDetails", product, tapNumber);
    }    
  }
};
</script>